import React, { useEffect, useState } from "react"
import dayjs from "dayjs"
import { graphql, useStaticQuery } from "gatsby"
import useAxios from "axios-hooks"
import { css } from "@emotion/react"
import styled from "@emotion/styled"
import { MDRender } from "../components/mdRender"
import Layout from "../components/layout"

interface TextNode {
  type: "TEXT"
  textNode: {
    content: string
  }
}

interface ParagraphNode {
  type: "PARAGRAPH"
  paragraphNode: {
    children: TextNode[]
  }
}

interface LineBreakNode {
  type: "LINE_BREAK"
  lineBreakNode: Record<string, never>
}

type Node = ParagraphNode | LineBreakNode

interface MemoProperty {
  hasLink: boolean
  hasTaskList: boolean
  hasCode: boolean
  hasIncompleteTasks: boolean
}

interface Memo {
  name: string
  state: string
  creator: string
  createTime: string
  updateTime: string
  displayTime: string
  content: string
  nodes: Node[]
  visibility: string
  tags: string[]
  pinned: boolean
  resources: any[]
  relations: any[]
  reactions: any[]
  property: MemoProperty
  snippet: string
}

interface MemosResponse {
  memos: Memo[]
  nextPageToken: string
}

const ListItem = styled.li`
  span {
    color: #2bbc8a;
  }
  p {
    margin-top: 0;
  }
  img {
    max-width: 100%;
    margin-top: 5px;
    border-radius: 2px;
    border: 1px solid #4e4e4e;
  }
`

const Divider = styled.div`
  margin: 12px 0;
`

const LoadMoreButton = styled.button`
  display: block;
  margin: 20px auto;
  padding: 4px 8px;
  background: #2bbc8a;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  &:hover {
    background: #239a6f;
  }
`

const ThinkPage = (props: any) => {
  const [pageSize] = useState(10)
  const [pageToken, setPageToken] = useState("")
  const [memos, setMemos] = useState<Memo[]>([])
  const [{ data, loading, error }] = useAxios(
    {
      url: "https://memo.ximing.ren/api/v1/memos",
      params: {
        pageSize,
        pageToken,
      },
    },
    { manual: typeof window === "undefined" }
  )
  useEffect(() => {
    if (data) {
      setMemos([...memos, ...data.memos])
    }
  }, [data])

  const { site } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  if (typeof window !== "undefined") {
    if (!data) {
      return (
        <Layout location={props.location} title={site.siteMetadata.title}>
          {error?.message}
        </Layout>
      )
    }
    const handleLoadMore = () => {
      if (data.nextPageToken) {
        setPageToken(data.nextPageToken)
      }
    }
    return (
      <Layout location={props.location} title={site.siteMetadata.title}>
        {memos.length === 0 && loading && <span className="dotting">...</span>}
        <section id="projects">
          <ul className="project-list">
            {memos.map((memo: Memo, index: number) => {
              return (
                <ListItem key={memo.name}>
                  {index !== 0 && <Divider />}
                  <span>
                    {dayjs(memo.createTime).format(`YYYY-MM-DD HH:mm`)}
                  </span>
                  <MDRender md={memo.content} />
                </ListItem>
              )
            })}
          </ul>
          {data.nextPageToken && (
            <LoadMoreButton onClick={handleLoadMore}>
              点击加载更多
              {memos.length && loading && <span className="dotting">...</span>}
            </LoadMoreButton>
          )}
        </section>
      </Layout>
    )
  }
  return (
    <Layout location={props.location} title={site.siteMetadata.title}>
      {""}
    </Layout>
  )
}

export default ThinkPage
